@import url('https://fonts.googleapis.com/css2?family=Encode+Sans&display=swap');

h1, h2, h3, h4, h5, h6, p, span  {
  font-family: 'Encode Sans', sans-serif;
}

h1 {
  font-size: xx-large;
}

h3, h6 {
  font-size: x-large;
}

p {
  font-size: large;
}

span {
  font-size: 24px;
}

button {
  height: auto !important;
}