.ant-modal-body{
  padding: 0px;

}

.ant-modal-content {
  border-radius: 32px;
}

.ant-modal-close-x {
  color: #ffffff;
}

body {
  margin: 0;
  height: 100vh;
  font-weight: 100;
  background: radial-gradient(#39a282,#6c5aa0);
}

body {
	background: linear-gradient(-45deg, #d15eff, #f08a2a, #3ce7ae, #91d523);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

