@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');
@cardBorderRadius: 32px;
@card_container_height: 300px;
@card_container_width: 250px;
@card_inner_container_height: 250px;
@card_inner_container_width: 200px;

@card_contents_height: 100px;

@wrapper_div_dimensions: 100%;

@showcase_height: 100%;
@showcase_max_height: 30vh;

@modal_content_border_radius: 32px;

@card_description_max_height: 24vh;

.card_style {
  border: none;
  border-radius: @cardBorderRadius;
  // &:hover {
  //   border: 1px solid #85beec;
  //   border-radius: @cardBorderRadius;
  // }
}

.card_row {
  max-height: @card_inner_container_height;
}

.card_contents {
  height: @card_contents_height;
}

.wrapper_div {
  width: @wrapper_div_dimensions;
  height: @wrapper_div_dimensions;
  background-color: transparent;
}

.showcased {
  height: @showcase_height;
  max-height: @showcase_max_height;
}

.modal_content {
  border-radius: @modal_content_border_radius;
}

.card_cover {
  border-radius: 32px 0px !important;
}

.card_description {
  max-height: @card_description_max_height;
  overflow: auto;
}

.technologiesAndTools {
  font-family: 'Poppins', sans-serif;
}

.card_description_overview {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

